* {
  padding: 0px;
  margin: 0px;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  border-radius: 5px;
  font-family: "Kanit", serif;
}

nav {
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: end;
  background: transparent;
}
nav ul {
  display: flex;
  padding-right: 5rem;
  justify-content: end;
  gap: 4rem;
}
nav ul li {
  font-size: 1.5em;
  cursor: pointer;
  color: black;
  font-weight: 600;
  &:hover {
    color: #49832e;
    text-decoration: underline;

  }
}
/* main */
main {
  height: 60rem;
  width: 99.99%;
}
#hero-img {
  width: 99.99%;
  height: 60em;
  position: absolute;
  top: 0px;
  z-index: -1;
}
.hero-text {
  position: relative;
  left: 5em;
  width: 50%;
  height: 35rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.hero-text h2 {
  font-size: 7rem;
  font-family: "Dancing Script", serif;
}
.hero-text h1 {
  font-size: 8rem;
  font-weight: 600;
}
.hero-text span {
  color: #49832e;
}
.hero-text a {
  border: 1px transparent;
  background: #49832e;
  padding: 1em 3em;
  display: inline-block;
  width: 18%;
  color: wheat;
  border-radius: 25px;
  transition: all 0.4s;
  box-shadow: 0px 0px 8px 5px rgba(0, 0, 0, 0.24);
  &:hover {
    transform: scale(1.1);
    box-shadow: 0px 0px 8px 5px rgba(0, 0, 0, 0.507);
  }
}
/* this is other */

.foods {
  height: 20rem;
  margin: 0 auto;
  width: 85%;
  display: flex;
  justify-content: space-between;
}
.foods section {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transition: all 0.3s;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.212), 0px 2px 4px rgba(0, 0, 0, 0.219),
    0px 4px 8px rgba(0, 0, 0, 0.1), 0px 8px 16px rgba(0, 0, 0, 0.1);
  &:hover {
    transform: scale(1.1);
  }
}
.arrow-img {
  width: 25rem;
  display: flex;
  justify-content: space-between;
  align-items: end;
}
.arrow-img img {
  height: 8em;
}
.arrow-img i {
  padding: 1.1em;
  border: transparent;
  border-radius: 51px;
  background-color: #49832e;
  color: white;
  cursor: pointer;
  box-shadow: 0px 0px 5px 3px rgba(0, 0, 0, 0.342);
}
.three-cards {
  width: 100%;
  padding-left: 1em;
}
.three-cards h2 {
  font-size: 2.5em;
}
.three-cards h3 {
  font-size: 1.5em;
}
.three-cards p {
  color: #49832e;
}

/*  */
.order-our-best {
  width: 85%;
  margin: 0 auto;
  padding-top: 7em;
  padding-bottom: 4em;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1em;
}
.heading {
  width: 85%;
  margin: 0 auto;
  font-size: 3rem;
  font-weight: 600;
}

/*  */

.productTitle {
  font-weight: 400;
  font-size: 1.7rem;
  color: black;
}
.productCard {
  width: 24%;
  height: 25em;
  display: flex;
  padding: 1em;
  flex-direction: column;
  transition: all 0.2s;
  justify-content: space-between;
  &:hover {
    transform: translateY(5px);
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.212),
      0px 2px 4px rgba(0, 0, 0, 0.219), 0px 4px 8px rgba(0, 0, 0, 0.1),
      0px 8px 16px rgba(0, 0, 0, 0.1);
  }
}
.productImage {
  height: 13em;
}

.brilliant-restuarnt {
  height: 20rem;
  width: 100%;
  background: #49832e;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1em;
}
.brilliant-restuarnt h2 {
  font-size: 4.5rem;
  color: white;
}
.brilliant-restuarnt a {
  padding: 1rem 4rem;
  color: white;
  background: #000;
}

.sectionhob {
  border: 1px solid red;
  display: flex;
  width: 90%;
  margin: 0 auto;
  flex-wrap: wrap;
  height: 70rem;
}

.footer {
  background-color: #f8f8f8;
  padding: 20px;
  text-align: center;
}

.footer-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.footer-section {
  flex: 1;
  margin: 10px;
}

.footer-section h2 {
  color: #333;
}

.footer-section p,
.footer-section ul {
  color: #666;
}

.footer-section ul {
  list-style-type: none;
  padding: 0;
}

.footer-section ul li {
  margin: 5px 0;
}

.footer-section ul li a {
  text-decoration: none;
  color: #49832e;
}

.footer-section ul li a:hover {
  text-decoration: underline;
}

.footer-bottom {
  margin-top: 20px;
  font-size: 14px;
  color: #999;
}
/* contact us */
.contact-us {
  background-color: #fff;
  padding: 40px;
  text-align: center;
}

.contact-container {
  max-width: 800px;
  margin: auto;
}

.contact-details {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.detail-item {
  flex: 1;
  margin: 20px;
  text-align: left;
}

.detail-item h3 {
  color: #333;
}

.detail-item p {
  color: #666;
}

.icon {
  width: 50px;
  height: auto;
}

.contact-form {
  display: flex;
  flex-direction: column;
}

.contact-form input,
.contact-form textarea {
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.contact-form button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.contact-form button:hover {
  background-color: #49832e; /* Darker shade on hover */
}

/* about us */
.about-us {
  background-color: #f9f9f9;
  padding: 40px;
  text-align: center;
}

.about-container {
  max-width: 800px;
  margin: auto;
}

.about-details {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.detail-item {
  flex: 1;
  margin: 20px;
  text-align: left;
}

.detail-item h3 {
  color: #333;
}

.detail-item p {
  color: #666;
}

.icon {
  width: 50px; /* Adjust size as needed */
  height: auto;
}

.about-image {
  margin-top: 20px;
}

.main-image {
  width: 100%;
  height: auto; /* Maintain aspect ratio */
  border-radius: 10px; /* Optional styling */
}

.about-container h3 {
  color: #49832e; /* Highlighting section headers */
}

.input-field {
  width: 85%;
  margin: 0 auto;
  padding: 3em;
  gap: 0.5em;
  display: flex;
  align-items: flex-end;
  justify-content: end;
}
.input-field input {
  padding: 0.6em;
}
.input-field button {
  padding: 0.8em 1em;
  outline: none;
  background: #49832e;
}

.productCard a {
  background-color: #49832e;
  color: white;
  padding: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1em;
  box-shadow: 0px 0px 5px 3px rgba(0, 0, 0, 0.342);
  cursor: pointer;
}
/* HTML: <div class="loader"></div> */
.loader {
  width: fit-content;
  font-weight: bold;
  font-family: sans-serif;
  font-size: 30px;
  padding-bottom: 8px;
  background: linear-gradient(currentColor 0 0) 0 100%/0% 3px no-repeat;
  animation: l2 2s linear infinite;
}
.loader:before {
  content:"Loading..."
}
@keyframes l2 {to{background-size: 100% 3px}}